*, *::before, *::after {
  box-sizing: border-box;
}
html, body, #root, .App {
  height: 100%;
}
#root {
  font: 14px "Century Gothic", Futura, sans-serif;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.square {
  position: relative;
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  min-height: 30px;
  min-width: 30px;
  height: 18vmin;
  width: 18vmin;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  cursor: pointer;
}
.square:focus {
  outline: none;
}
.square:hover {
  border: 2px solid #000;
  z-index: 1;
}
.filled {
  cursor: default;
}
.filled:hover{
  border: 1px solid #999;
}
.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.game-board {
  display: flex;
  justify-content: center;
}

.game-info {
  width: 150px;
}

.history-button {
  background-color: transparent;
  cursor: pointer;
  border: none;
  padding: 5px 0;
}
.history-button:hover {
  font-weight: bold;
}

@media screen and (max-width: 335px) {
  .game {
    flex-direction: column;
    justify-content: unset;
    padding-top: 25%;
  }
}

@media screen and (max-height: 440px) {
  .game {
    padding-top: 1px;
  }
}